$white: #fff;
$black: #000;
$black-russian: #000106;
$radical-red: #fe4a5b;
$dusty-gray: #989797;
$alto: #e0e0e0;
$chicago: #575756;
$emperor: #525151;
$sandal: #a68e6f;
$mongoose: #b3a087;
$swirl: #d3ccc2;

:root {
	--white: #{$white};
	--black: #{$black};
	--black-russian: #{$black-russian};
	--radical-red: #{$radical-red};
	--dusty-gray: #{$dusty-gray};
	--alto: #{$alto};
	--chicago: #{$chicago};
	--emperor: #{$emperor};
	--sandal: #{$sandal};
	--mongoose: #{$mongoose};
	--swirl: #{$swirl};
}

$body-bg: $white;
$global-margin: 20px;
$global-padding: 20px;
$gutter-width: 20px;
$font-size-root: 15px;
$global-line-height: 23 / 15;
$font-base: 'PF Agora Sans Pro';
$font-family-base: $font-base, sans-serif;

$xxxs: 0;
$xxs:  320px + $global-padding * 2;
$xs:   480px;
$s:    600px;
$m:    768px;
$l:    1000px + $global-padding * 2;
$xl:   1280px - $global-padding * 2;
$xxl:  1600px - $global-padding * 2;
$xxxl: 1920px - $global-padding * 2;

$container-max-width: $l;

// ---------- Asymptotic Fibonacci numbers  ----------

$fibonacci-number: 1.61803398875;
$fibo-asymp-xxxl: number(str-replace(#{$fibonacci-number - 1 / 1}, '-', '')); // 0.61803398875
$fibo-asymp-xxl: number(str-replace(#{$fibonacci-number - 2 / 1}, '-', '')); // 0.381966011
$fibo-asymp-xl: number(str-replace(#{$fibonacci-number - 3 / 2}, '-', '')); // 0.118033989
$fibo-asymp-l: number(str-replace(#{$fibonacci-number - 5 / 3}, '-', '')); // 0.048632678
$fibo-asymp-m: number(str-replace(#{$fibonacci-number - 8 / 5}, '-', '')); // 0.018033989
$fibo-asymp-s: number(str-replace(#{$fibonacci-number - 13 / 8}, '-', '')); // 0.006966011
$fibo-asymp-xs: number(str-replace(#{$fibonacci-number - 21 / 13}, '-', '')); // 0.002649373
$fibo-asymp-xxs: number(str-replace(#{$fibonacci-number - 34 / 21}, '-', '')); // 0.00101363
$fibo-asymp-xxxs: number(str-replace(#{$fibonacci-number - 55 / 34}, '-', '')); // 0.00038693

// ---------- Asymptotic Fibonacci percentage  ----------

$fibo-asymp-xxxl-percentage: $fibo-asymp-xxxl * 100%; // 61.803398875%
$fibo-asymp-xxl-percentage: $fibo-asymp-xxl * 100%; // 38.1966011%
$fibo-asymp-xl-percentage: $fibo-asymp-xl * 100%; // 11.8033989%
$fibo-asymp-l-percentage: $fibo-asymp-l * 100%; // 4.8632678%
$fibo-asymp-m-percentage: $fibo-asymp-m * 100%; // 1.8033989%
$fibo-asymp-s-percentage: $fibo-asymp-s * 100%; // 0.6966011%
$fibo-asymp-xs-percentage: $fibo-asymp-xs * 100%; // 0.2649373%
$fibo-asymp-xxs-percentage: $fibo-asymp-xxs * 100%; // 0.101363%
$fibo-asymp-xxxs-percentage: $fibo-asymp-xxxs * 100%; // 0.038693%
